export function mulberry32(a) {
    return function () {
        var t = a += 0x6D2B79F5;
        t = Math.imul(t ^ t >>> 15, t | 1);
        t ^= t + Math.imul(t ^ t >>> 7, t | 61);
        return ((t ^ t >>> 14) >>> 0) / 4294967296;
    }
}

//const d = new Date();
export const random = mulberry32(1)


const exchange = function (a, i, j) {
    var temp = a[i];
    a[i] = a[j];
    a[j] = temp;
};

const shuffle = function (a) {
    var n = a.length;
    for (var i = 1; i < n; ++i) {
        var j = Math.floor(random() * (1 + i));
        exchange(a, i, j);
    }
};

const s4 = function () {
    return Math.floor((1 + random()) * 0x10000)
        .toString(16)
        .substring(1);
}

const guid = function () {
    return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
};

class MinPQ{
    s: any[];
    N: number;
    compare: any;

    constructor(compare){
    if (!compare) {
        compare = function (a1, a2) {
            return a1 - a2;
        };
    }
    this.s = [];
    this.N = 0;
    this.compare = compare;
    }
    
    less(a1, a2) {
        return this.compare(a1, a2) < 0;
    };

    enqueue(item) {
        while (this.s.length <= this.N + 1) {
            this.s.push(0);
        }
        this.s[++this.N] = item;
        this.swim(this.N);
    }

        
    delMin() {
        if (this.N == 0) {
            return null;
        }
    
        var item = this.s[1];
        exchange(this.s, 1, this.N--);
        this.sink(1);
        return item;
    };
    min() {
        if (this.N == 0) {
            return null;
        }
        return this.s[1];
    };

    sink(k) {
        while (k * 2 <= this.N) {
            var child = k * 2;
            if (child < this.N && this.less(this.s[child+1], this.s[child])){
                child++;
            }
            if(this.less(this.s[child], this.s[k])){
                exchange(this.s, child, k);
                k = child;
            } else {
                break;
            }
        }  
    }
    


    swim(k) {
        while (k > 1) {
            var parent = Math.floor(k / 2);
            if (this.less(this.s[k], this.s[parent])) {
                exchange(this.s, k, parent);
                k = parent;
            } else {
                break;
            }
        }
    };

    clear() {
        this.s = [];
        this.N = 0;
    };

    size() {
        return this.N;
    };

    isEmpty() {
        return this.N == 0;
    };
    
}

class StackNode{
    value: any;
    next: any;

    constructor(value){
        this.value = value;
        this.next = null;
    };

}

class Stack{
    N: number;
    first: any;
    constructor(){
        this.N = 0;
        this.first = null;
    }

    push(item) {
        var oldFirst = this.first;
        this.first = new StackNode(item);
        this.first.next = oldFirst;
        this.N++;
    };

    pop(item) {
        var oldFirst = this.first;
        if (oldFirst == null) {
            return null;
        }
        var item = oldFirst.value;
        this.first = oldFirst.next;
        this.N--;
        return item;
    };

    clear() {
        this.N = 0;
        this.first = null;
    };

    size(){
        return this.N;

    }

    isEmpty() {
        return this.N == 0;
    };
}

class QueueNode{
value: any;
next: any;

constructor(value) {
    this.value = value;
    this.next = null;
}

}

class Queue{
    first: any;
    last: any;
    N: number;

    constructor(){
        this.first = null;
        this.last = null;
        this.N = 0;
    }

    enqueue(item) {
        var oldLast = this.last;
        this.last = new QueueNode(item);
        if (oldLast != null) {
            oldLast.next = this.last;
        }
        if (this.first == null) {
            this.first = this.last;
        }
        this.N++;
    };


    dequeue() {
        var oldFirst = this.first;
        if (oldFirst == null) {
            return null;
        }
        var item = oldFirst.value;
        this.first = oldFirst.next;
        if (this.first == null) {
            this.last = null;
        }
        this.N--;
        return item;
    };


    clear() {
        this.first = null;
        this.last = null;
        this.N = 0;
    };


    isEmpty() {
        return this.N == 0;
    };


    size() {
        return this.N;
    };
    
}

export class SimEvent{
time: number;
rank: any;
    id: any;
    scheduler: Scheduler;

constructor(rank : null | number = null){
    this.time = 0;
    if (rank) {
        this.rank = rank;
    } else {
        this.rank = 1;
    }
}

guid() {
    if (this.id) {
        return this.id;
    } else {
        this.id = guid();
        return this.id;
    }
};

sendMsg(recipient, message) {
    this.scheduler.messenger.sendMsg(this.guid(), recipient, message);
};


readInBox() {
    return this.scheduler.messenger.readInBox(this.guid());
};


}


export class Scheduler{
    pq: MinPQ;
    current_time: any;
    current_rank: any;
    messenger: any;


    constructor() {
        this.pq = new MinPQ(function (evt1, evt2) {
            var time_diff = evt1.time - evt2.time;
            if (time_diff == 0) {
                return evt2.rank - evt1.rank;
            } else {
                return time_diff;
            }
        });
        this.current_time = null;
        this.current_rank = null;
        this.messenger = new Messenger(this);
    };


    getNextTime() : number | null {
        if(this.pq.isEmpty())
            return null
        else
            return this.pq.min().time

    }

    update(){
        var current_rank = null;
        var current_time = null;
    
        while (!this.pq.isEmpty()) {
            var evt = this.pq.min();
            var time = evt.time;
            var rank = evt.rank;
            if (current_time == null) {
                current_time = time;
            } else if (current_time != time) {
                break;
            }
    
            this.update_mini();
        }
    
        this.messenger.update(0);
    }

    update_mini() {
        var current_time = null;
        var current_rank = null;
        var events: any[] = [];
        while (!this.pq.isEmpty()) {
            var evt = this.pq.min();
            var time = evt.time;
            var rank = evt.rank;
            if (current_time == null) {
                current_time = time;
            } else if (current_time != time) {
                break;
            }
    
            if (current_rank == null) {
                current_rank = rank;
            } else if (current_rank != rank) {
                break;
            }
    
            events.push(this.pq.delMin());
        }
    
        shuffle(events);
    
        var old_time = this.current_time;
        if (events.length > 0) {
            this.current_time = current_time;
            this.current_rank = current_rank;
        }
    
        for (var i = 0; i < events.length; ++i) {
            var deltaTime: number | null = 0;
            if (this.current_time != null) {
                if(current_time === null)
                    throw new Error("should not be null!")
                deltaTime = current_time - old_time;
            } else {
                deltaTime = current_time;
            }
            if (!events[i].update) {
                console.log('event does not define update(deltaTime) method!!!');
            } else {
                events[i].update(deltaTime);
            }
        }
    
    
    
        for (var i = 0; i < events.length; ++i) {
            if (events[i].repeatInterval) {
                this.scheduleRepeatingAt(events[i], this.current_time + events[i].repeatInterval, events[i].repeatInterval);
            }
        }
    
        return events;
    };


    schedule(evt, time) {
        evt.time = time;
        if (!evt.scheduler) {
            evt.scheduler = this;
        }
        this.pq.enqueue(evt);
    };

    hasEvents() {
        return !this.pq.isEmpty();
    };
// Method that schedules an event to fire once at delta time later (than the current time) 
    scheduleOnceIn(evt, deltaTime) {
        var start_time = this.current_time;
        if (this.current_time == null) {
            start_time = 0;
        }
        if (!evt.scheduler) {
            evt.scheduler = this;
        }
        evt.time = start_time + deltaTime;
        this.pq.enqueue(evt);
    };
    
// Method that schedules an event to fire at interval of delta time from now on (other than the current time)

    scheduleRepeatingIn(evt, deltaTime) {
        var start_time = this.current_time;
        if (this.current_time == null) {
            start_time = 0;
        }
        if (!evt.scheduler) {
            evt.scheduler = this;
        }
        this.scheduleRepeatingAt(evt, start_time + deltaTime, deltaTime);
    };

// Method that schedules an event to fire at interval of delta time, with first event fired at the time specified
    scheduleRepeatingAt(evt, startTime, deltaTime) {
        evt.time = startTime;
        evt.repeatInterval = deltaTime;
        if (!evt.scheduler) {
            evt.scheduler = this;
        }
        this.pq.enqueue(evt);
    };


    reset(){
        this.current_rank = null;
        this.current_time = null;
        this.pq.clear();
        this.messenger.reset();
    };
}




class Vector2D{
    x: any;
    y: any;
   constructor(x, y) {
    this.x = x;
    this.y = y;
};


    distance(that) {
        var dx = this.x - that.x;
        var dy = this.y - that.y;
        return Math.sqrt(dx * dx + dy * dy);
    };


    length() {
        return Math.sqrt(this.x * this.x + this.y * this.y);
    };

    resize(len) {
        var current_len = this.length();
        if (current_len == 0) {
            return;
        }
        var ratio = len / current_len;
    
        this.x *= ratio;
        this.y *= ratio;
    };
    
    
    addIn(f) {
        this.x += f.x;
        this.y += f.y;
    };


    set(x, y) {
        this.x = x;
        this.y = y;
    };
}


