import {Entering, firstStep, FoodStation, Person, SimulationState, Table} from "./model";

import * as d3 from "d3";
import {BaseType, ScaleLinear, Selection} from "d3";
import { Scheduler } from "./jssim";


import Chart, { registerables } from "chart.js/auto";


const getCircularReplacer = () => {
    const seen = new WeakSet();
    return (key, value) => {
        if (typeof value === "object" && value !== null) {
            if (seen.has(value)) {
                return;
            }
            seen.add(value);
        }
        return value;
    };
};


const sizeTable: number = 20

const sizeFoodServing: number = 50

const sizePerson: number = 10

const distanceBetweenPeople: number = 30

const SERVING_TIME = 0.5;

console.log("You are reading me now")


export type GUIState = {
    model: SimulationState;

    timeDisplayed: number;

    personLocations: Map<Person, { x: number, y: number }>;

    containerSVG: Selection<SVGSVGElement, unknown, HTMLElement, any>

    //this should give me the x mapping for the position of tables
    horizontalScaleMap: ScaleLinear<number, number>

    //this maps a table or food station ---> index, which is useful
    //when we want to know the y location of a table/foodstation without searching
    //through the list in the simulation state
    tableIndices: Map<Table | FoodStation, number>
    /**
     * the div element we can send infos to
     */
    infoDiv: Selection<HTMLDivElement, unknown, HTMLElement, any>
    //todo add height info to resize things
};




//verticalScaleMap will have
// (i) entrance table (ii) normal tables (iii) food stations

let isPlaying: boolean = false

function updateGUI(gui: GUIState) {


    //map food stations and tables in....
    //assume 0 is the queue to enter....
    for (let i = 0; i < gui.model.foodStations.length; i++) {
        gui.tableIndices.set(gui.model.foodStations[i], i + 1)
    }
    for (let i = 0; i < gui.model.allTables.length; i++) {
        gui.tableIndices.set(gui.model.allTables[i], i + 1 + gui.model.foodStations.length)
    }

    function getTableIndex(d: FoodStation | Table) {
        return gui.tableIndices.get(d) ?? 0;
    }

    //now update the linear mapping a bit
    gui.horizontalScaleMap = d3.scaleLinear()
        .domain([0, 1 + gui.model.foodStations.length + gui.model.allTables.length]).
        range([100, 800])



    //draw the food stations
    const foodStationRects = gui.containerSVG.selectAll('.food-station')
        .data(gui.model.foodStations).join("rect").
        attr('class', 'food-station').attr('width', 50).attr('height', 50)
        .attr('x', function (d) {
            return gui.horizontalScaleMap(getTableIndex(d) );
        }).attr('y', 50)
        .attr('fill', "red");


    //draw tables
    const tableCircles = gui.containerSVG.selectAll('.table')
        .data(gui.model.allTables)
        .join('circle')
        .attr('class', 'table')
        .attr('r', 20)
        .attr('cx', d => gui.horizontalScaleMap(getTableIndex(d) ))
        .attr('cy', d => 50)
        .attr('fill', 'gray');

    tableCircles.append('title').
    text(d => JSON.stringify(d, getCircularReplacer()));

    //now model the position of all people
    gui.personLocations.clear()
    console.log(gui.personLocations)

    //1: those that are still waiting
    let waitingIndex = 0;
    //start with people who are ABOUT to enter (they are not waiting for a table but haven't reached it yet!)
    for(const p of gui.model.peopleInStore){
        if(p.currentAction instanceof Entering){
            if(gui.personLocations.has(p))
                throw new Error(`${p} has already a position!`)
            gui.personLocations.set(p,
                {x: gui.horizontalScaleMap(0),
                    y: sizeFoodServing + distanceBetweenPeople * (waitingIndex+1)}
            )
            waitingIndex += 1
        }
    }
    for(let group of gui.model.groupsWaitingToBeServed){
        for(const person of group.members){
            if(gui.personLocations.has(person))
                throw new Error(`${person} has already a position!`)
            gui.personLocations.set(person,
                {x: gui.horizontalScaleMap(0),
                    y: sizeFoodServing + distanceBetweenPeople * (waitingIndex+1)}
            )
            waitingIndex += 1
        }
    }
    //2: those that are waiting at food tables
    for(const fs of gui.model.foodStations)
    {
        let waitingStationIndex = 0;
        for(const person of fs.queue){
            if(gui.personLocations.has(person))
                throw new Error(`${person} has already a position!`)
            gui.personLocations.set(person,
                {x: gui.horizontalScaleMap(getTableIndex(fs)),
                    y: sizeFoodServing + distanceBetweenPeople * (waitingStationIndex+1)}
            )
            waitingStationIndex += 1
        }
    }
    //3: those that are at tables
    for(const table of gui.model.allTables)
    {
        let waitingStationIndex = 0;
        for(const person of table.sitting){
            if(gui.personLocations.has(person))
                throw new Error(`${person} has already a position!`)
            gui.personLocations.set(person,
                {x: gui.horizontalScaleMap(getTableIndex(table)),
                    y: sizeFoodServing + distanceBetweenPeople * (waitingStationIndex+1)}
            )
            waitingStationIndex += 1
        }
    }
    console.log(gui.personLocations)
    console.log(gui.model.peopleInStore)
    //now turn them into circles


    const personCircles: Selection<BaseType, Person, SVGSVGElement, unknown> = gui.containerSVG.selectAll('.person')
        .data(gui.model.peopleInStore, d => (d as Person).id);

    personCircles.exit()
        .transition()
        .duration(200)
        .style('opacity', 0)
        .attr('transform',`translate(0,999)`)
    //  .remove()
    ;
    let personEnter = personCircles.enter()
        .append('g').attr('class', 'person')
        .attr('transform',
            d => `translate(${(gui.personLocations.get(d)?.x  ?? 100)},${gui.personLocations.get(d)?.y ?? 100})`);


    personEnter.append('circle')
        .attr('r', 10)
        .attr('fill', 'black')
        .attr('stroke', 'white')
        .attr('stroke-width', 2)

    personEnter.append('text')
        .attr('x', 0)
        .attr('y', 4)
        .attr('text-anchor', 'middle')
        .attr('fill', 'white')
        .text(d => d.id);

    personEnter.merge(personCircles)
        .transition()
        .duration(200)
        .attr('transform',
            d => `translate(${(gui.personLocations.get(d)?.x  ?? 100)},${gui.personLocations.get(d)?.y ?? 100})`);


    //add event listeners to person circles
    personCircles
        .on("mouseover", function (event, d) {

            // Show the tooltip on hover
            gui.infoDiv.transition().duration(50).style("opacity", 0.9);
            gui.infoDiv.html(
                // "<pre>" + JSON.stringify(d, getCircularReplacer(),2) + "</pre>"
                `
    <h3> Person Info </h3>
    <dl>
    <dt>ID</dt>
    <dd>${d.id}</dd>
    <dt>Current Action</dt>
    <dd>${d.currentAction.constructor.name}</dd>
    </dl>`

            )
                .style("left", `${event.pageX}px`)
                .style("top", `${event.pageY}px`);
        })
        .on("mouseout", function () {
            // Hide the tooltip on mouseout
            gui.infoDiv.transition().duration(100).style("opacity", 0);
        });

    //now please also add something about time of the simulation
    // gui.containerSVG.selectAll('.timestamp')
    //     //.remove()
    //     .data([gui.model.scheduler.current_time])
    //     .join('text')
    //     .attr("class","timestamp")
    //     .attr('x', 800)
    //     .attr('y', 480)
    //     .attr('text-anchor', "end")
    //     .attr('style', 'font-size: 20px; font-family: Arial;')
    //     .text(d => "Minutes passed: " + d.toFixed(2))
    const stepCounter: HTMLSpanElement = document.getElementById("time-step-counter");
    stepCounter.innerText = "Minutes passed: " + gui.model.scheduler.current_time.toFixed(2)

}




export function buildGUI(numberOfTables: number, numberOfFoodStations:number, newGuestsArrivalTime:number){

    const scheduler = new Scheduler();
    scheduler.reset();

    let tables: Table[] = []
    for(let i=0; i<numberOfTables; i++)
        tables.push({ group: null, sitting: [] })

    let foodStations: FoodStation[] = []
    for(let i=0; i<numberOfFoodStations; i++)
        foodStations.push( { queue: [], servingTime: SERVING_TIME, inUse: false })


    const state: SimulationState = {
        scheduler: scheduler,
        peopleInStore: [],
        allGroups: [],
        //3 tables?
        allTables: tables,
        peopleWhoLeft: [],
        totalWasteInKg: 0,
        foodStations: foodStations,
        groupsWaitingToBeServed: [],
        simulationParameters : {
            enteringTime: 0.25,
            foodGrabbedInKg: 0.5,
            eatingTime: 5,
            setupTime: 1.5,
            groupSize: 4,
            tableToFoodStationTime: 0.9,
            arrivalInterval: newGuestsArrivalTime,
            wastePercentage: 0.2
        }
    };



    scheduler.scheduleRepeatingIn(firstStep(state), 1);

    const svg: Selection<SVGSVGElement, unknown, HTMLElement, any> =
        d3.select('#svgdiv')
            //    .append('div').attr('id','svgdiv').attr('class','pure-g')
            //    .append('div').attr('id','svgdiv2').attr('class','pure-u-1')
            .append('svg')
            .attr('class','.pure-img')
            // .attr('width','800')
            // .attr("preserveAspectRatio", "xMinYMin meet")
            .attr("viewBox", "0 0 800 500")
            // .attr('width', 800)
            //.attr('height', 600)
            .attr("id","simulation_svg");

    const tooltip: Selection<HTMLDivElement, unknown, HTMLElement, any> =
        d3.select("body")
            .append("div")
            .style("position", "absolute")
            .attr("class", "tooltip")
            .style("opacity", 0);


    const gui : GUIState = {
        model: state,
        timeDisplayed: 0,
        personLocations: new Map(),
        containerSVG: svg,
        horizontalScaleMap: d3.scaleLinear()
            .domain([0, 1 + state.foodStations.length + state.allTables.length]).
            range([100, 500]),
        tableIndices: new Map(),
        infoDiv: tooltip
    }

    console.log("setting the interval....")

    const GUI_TIME_STEP_SIZE = 1
    //setup the chart
    Chart.register(...registerables);
    const canvasElement = <HTMLCanvasElement>
        document.getElementById("wastechart");
    const config = {
        type: "line",
        data: {
            labels: [],
            datasets: [{
                label: 'Cumulative Waste',
                data: [],
                fill: false,
                borderColor: 'rgb(75, 192, 192)',
                tension: 0.1
            }]
        },
        options: {
            aspectRatio: 1.3,
            scales: {
                x: {
                    type: "linear",
                    position: "bottom",
                    text: "Simulated time",
                },
                y: {
                    type: "linear",
                    text: "Total Food wasted",
                    ticks: {
                        // Include a kg sign in the ticks
                        callback: function (value, index, ticks) {
                            return value + " kg";
                        },
                    },
                },
            },
        },
    };
    const guiChart = new Chart(
        canvasElement,
        config
    )

    //setup the play button
    const button: HTMLButtonElement = document.createElement("button")
    button.classList.add("pure-button")
    const playIcon = document.createElement("i")
    playIcon.id = "play-icon"
    playIcon.classList.add("fas","fa-play","fa-3x")

    button.id = "pause-resume-button"
    button.appendChild(
        playIcon
    )
    document.getElementById("buttonHolder").appendChild(button)
 //   const button = document.getElementById("pause-resume-button");
//    const playIcon = document.getElementById("play-icon");


    button.addEventListener("click", () => {
        isPlaying = !isPlaying;

        if (isPlaying) {
            playIcon.classList.remove("fa-play");
            playIcon.classList.add("fa-pause");
            // Start the simulation
        } else {
            playIcon.classList.remove("fa-pause");
            playIcon.classList.add("fa-play");
            // Pause the simulation
        }
    });

    // Step 4: call update function in a loop
    setInterval(() => {
        if(!isPlaying)
            return null

        // if(gui.timeDisplayed > 200)
        //     return null;

        //while(true){
        const nextActionTime = gui.model.scheduler.getNextTime()
        console.log(nextActionTime)

        //  if(nextActionTime == null || nextActionTime > gui.timeDisplayed + GUI_TIME_STEP_SIZE )
        //    break;
        gui.model.scheduler.update()
        //}
        gui.timeDisplayed+=GUI_TIME_STEP_SIZE
        console.log("update GUI...")
        updateGUI(gui); // update visualization

        //update graph
        guiChart.data.labels.push(gui.timeDisplayed);
        guiChart.data.datasets[0].data.push(state.totalWasteInKg);
        guiChart.update();

    }, 100); // run every 100ms

    console.log("done setting the interval....")
}
