import {Scheduler, SimEvent, random} from "./jssim"



const asserts = true;

let idCounter = 0;


export type SimulationParameters = {
  eatingTime: number;

  tableToFoodStationTime: number;

  enteringTime: number;

  setupTime: number;

  foodGrabbedInKg: number;

  wastePercentage: number;

  groupSize: number;

  arrivalInterval: number;

}


export type SimulationState = {
  scheduler: Scheduler;

  peopleInStore: Person[];

  allGroups: Group[];

  allTables: Table[];

  peopleWhoLeft: Person[];

  totalWasteInKg: number;

  foodStations: FoodStation[];

  groupsWaitingToBeServed: Group[];

  simulationParameters: SimulationParameters;
};

export const generateNewGroup = function (state: SimulationState) {
  const newGroup = new Group();
  for (let i = 0; i < state.simulationParameters.groupSize; i++) {
    const person = {
      id: (idCounter++).toString(),
      amountEatenInKg: 0,
      foodWastedInKg: 0,
      currentAction: null,
      group: newGroup,
      plateHeld: null,
      wastePercentage: state.simulationParameters.wastePercentage,
    };
    newGroup.members.push(person);
    state.peopleInStore.push(person)

  }

  //see if there is an empty table!
  const freeTable: Table | undefined = state.allTables.find(
    (table) => table.group == null
  );
  if (freeTable === undefined) {
    //there is no free table, got to wait now
    state.groupsWaitingToBeServed.push(newGroup);
  } else {
    //there is a table, start everyone!
    sitNewGroupAtNewTable(newGroup, freeTable, state);
  }

  //get ready to start the next group...
  state.scheduler.scheduleOnceIn(
    new (class extends SimEvent {
      update() {
        // implement here
        console.log("New group at time " + state.scheduler.current_time);
      }
    })(),
    state.simulationParameters.arrivalInterval
  );
};

export type ActionResolution = {
  previousAction: PersonAction; // the action that was taken previously
  nextAction: PersonAction | null; // the action to take next, if any
  /**
   * either I will perform the next action
   * (i) immediately
   * (ii) never until called - queue
   * (iii) after a specified delay (in minutes)
   */
  waitType: "immediately" | "queue" | number; // specifies when the next action will be taken
};

// Abstract class that represents an action a person can take
export abstract class PersonAction {
  person: Person;

  constructor(person: Person) {
    this.person = person;
  }

  /**
   * do the action and returns the next action to take and when
   * @param state holds simulation information, if needed
   */
  abstract act(state: SimulationState): ActionResolution;
}

// Represents the action of a person entering the store and sitting at a table
export class Entering extends PersonAction {
  act(state: SimulationState): ActionResolution {
    //sit at table, get ready to go out again
    if (this.person.group.assignedTable == null)
        // if the person does not have an assigned table, throw an error
      throw new Error("didn't give this person a table!");
    if (
      asserts &&
      this.person.group.assignedTable.sitting.includes(this.person)
    )
        // if the person is already sitting at a table, throw an error
      throw new Error("This person is trying to sit twice!!");

    //sit at table
    this.person.group.assignedTable.sitting.push(this.person);

    //now prepare to leave!
    return {
      previousAction: this, // previous action was Entering
      nextAction: new LookForFood(this.person), // next action is to look for food
      waitType: state.simulationParameters.setupTime, // take the next action after a delay specified by simulationParameters.setupTime
    };
  }
}

// Represents the action of a person leaving the store
export class Exiting extends PersonAction {
  act(state: SimulationState): ActionResolution {
    if (this.person.group.assignedTable == null)
        // if the person does not have an assigned table, throw an error
      throw new Error("This person is trying to exit without having a table");

    if (
      asserts &&
      !this.person.group.assignedTable.sitting.includes(this.person)
    )

      throw new Error("This person is standing up without ever sitting down!");
    this.person.group.assignedTable.sitting =
      this.person.group.assignedTable.sitting.filter(
        (person) => person != this.person
      ); // remove the person from the list of people sitting at the assigned table

    //we are being told to leave, so let's do that
    state.peopleInStore = state.peopleInStore.filter(
      (item) => item != this.person
    );  // remove the person from the list of people in the store

    //add ourselves to the list of people who left
    state.peopleWhoLeft.push(this.person);
  // increment the number of people who left in the person's group
    this.person.group.numberOfPeopleWhoLeft += 1;

    return {
      previousAction: this,
      nextAction: null,
      waitType: "queue",
    };
  }
}

/**
 * go to finished eating with a delay
  * delay represents how much it takes you to eat
 
 */
export class StartEating extends PersonAction {
  //start eating and wait till you are done
  act(state: SimulationState): ActionResolution {
    return {
      previousAction: this,
      waitType: state.simulationParameters.eatingTime,
      nextAction: new FinishedEating(this.person),
    };
  }
}

/**
 * This class represents the action of finishing eating. It calculates the amount of food wasted, updates the
 * relevant values, and returns an ActionResolution object where you wait for group consensus after a delay
 */
export class FinishedEating extends PersonAction {
  act(state: SimulationState): ActionResolution {
    //let's eat
    let plate = this.person.plateHeld;
    if (plate == null) throw new Error("Trying to eat without a plate!");
    let waste = plate.foodOnPlateInKG * this.person.wastePercentage;
    //account for waste:
    this.person.foodWastedInKg += waste;
    state.totalWasteInKg += waste;
    //account for eating
    this.person.amountEatenInKg += plate.foodOnPlateInKG - waste;
    this.person.plateHeld = null;
    //now wait it out...
    return {
      previousAction: this,
      waitType: "queue",
      nextAction: new WaitingForConsensus(this.person),
    };
  }
}

/**
 * queuing up, waiting to choose whether to eat more. When it's act time
 * expect to be told what to do via setter first, then either go back to serving
 * or quit
 */
export class WaitingForConsensus extends PersonAction {
  decidedToGo: boolean | null = null;

  act(state: SimulationState): ActionResolution {
    if (asserts && this.decidedToGo == null) {
      //if waiting ended and there was no decision, throw error
      throw new Error("Stopped waiting without deciding where to go!");
    }
    if (this.decidedToGo == false)
      return {
        previousAction: this,
        waitType: "immediately",
        nextAction: new Exiting(this.person),
      };
    else
      return {
        previousAction: this,
        waitType: "immediately",
        nextAction: new LookForFood(this.person),
      };
  }
}

/**
 * This class represents the action of looking for food. It selects the food station with the fewest people and
 * adds the person to its queue. It returns an ActionResolution object with information about the action performed
 * and what comes next.
 */
export class LookForFood extends PersonAction {
  //queue up
  act(state: SimulationState): ActionResolution {
    if (this.person.group.assignedTable == null)
        //if person doesn't have a table assigned, throw error

      throw new Error("didn't give this person a table to stand from!");
    //if person is standing without sitting down, throw error
    if (
      asserts &&
      !this.person.group.assignedTable.sitting.includes(this.person)
    )
      throw new Error("This person is standing up without ever sitting down!");
    this.person.group.assignedTable.sitting =
      this.person.group.assignedTable.sitting.filter(
        (person) => person != this.person
      );

    //select the food station with the shortest queue
    let bestFoodStation: FoodStation = state.foodStations[0];
    let shortestQueue: number = bestFoodStation.queue.length;
    for (let i = 1; i < state.foodStations.length; i++) {
      if (shortestQueue == 0) break;
      const queueLength = state.foodStations[i].queue.length;
      if (queueLength < shortestQueue) {
        shortestQueue = queueLength;
        bestFoodStation = state.foodStations[i];
      }
    }

    // Join the queue of the selected food station
    bestFoodStation.queue.push(this.person);

    // Return the next action which is to queue at the selected food station
    return {
      previousAction: this,
      waitType: "queue",
      nextAction: new QueuingAtStation(this.person, bestFoodStation),
    };
  }
}

export class QueuingAtStation extends PersonAction {
  foodStation: FoodStation;

  constructor(person: Person, foodStation: FoodStation) {
    super(person);
    this.foodStation = foodStation;
  }

  act(state: SimulationState): ActionResolution {
    //we are being called
    if (
      asserts &&
      (!this.foodStation.inUse || this.foodStation.queue[0] != this.person)
    )
      throw new Error("Serving the wrong person!");

    //you also shouldn't have any plate
    if (asserts && this.person.plateHeld != null)
      throw new Error("Double serving not implemented yet!");
    //grab the food and leave

    //grabbed!
    this.person.plateHeld = {
      foodOnPlateInKG: state.simulationParameters.foodGrabbedInKg,
    };

    this.foodStation.inUse = false;
    this.foodStation.queue.shift();

    if (this.person.group.assignedTable == null)
      throw new Error("didn't give this person a table!");
    if (
      asserts &&
      this.person.group.assignedTable.sitting.includes(this.person)
    )
      throw new Error("This person is trying to sit twice!!");
    this.person.group.assignedTable.sitting.push(this.person);

    //now we leave!
    return {
      nextAction: new StartEating(this.person),
      previousAction: this,
      waitType: state.simulationParameters.tableToFoodStationTime,
    };
  }
}

export type Person = {
  /**
   * plain id of the person, useful for logs
   */
  id: string;

  /**
   * total weight of food currently eaten
   */
  amountEatenInKg: number;

  /**
   * amountOfFoodWasted
   */
  foodWastedInKg: number;

  /**
   * tag for what I am currently doing
   */
  currentAction: PersonAction | null;

  /**
   * group of people I belong to (associated with the table I eat at)
   */
  group: Group;

  plateHeld: Plate | null;

  /**
   * this will have to change....
   */
  wastePercentage: number;
};

export type Plate = {
  foodOnPlateInKG: number;
};

export class Group {
  /**
   * the people that make up this group
   */
  members: Person[] = [];

  numberOfPeopleWhoLeft = 0;

  assignedTable: Table | null = null;

  shallWeEatMore(): boolean {
    return random() < 0.2;
  }

  isEverybodyWaitingForADecision(): boolean {
    for (const member of this.members) {
      if (!(member.currentAction instanceof WaitingForConsensus)) {
        return false;
      }
    }
    return true;
  }

  isEverybodyGone(): boolean {
    return this.numberOfPeopleWhoLeft >= this.members.length;
  }

  //todo decision on whether to eat more or quitting
  //if quitting then trigger exit action for everyone...
  //leave the table
  //self destroy?
}

export type Table = {
  /**
   * who is assigned to this table
   */
  group: Group | null;

  /**
   * currently sitting here
   */
  sitting: Person[];
};

export type FoodStation = {
  queue: Person[];

  servingTime: number;

  inUse: boolean;
};

class StateMachineStep extends SimEvent {
  action: PersonAction;

  state: SimulationState;

  constructor(action: PersonAction, state: SimulationState) {
    super();
    this.action = action;
    this.state = state;
  }

  update() {
    console.log(
      "At time " +
        this.state.scheduler.current_time +
        " person " +
        this.action.person.id +
        " is just about to accomplish action " +
        this.action.constructor.name
    );
    console.log(
      "next action will occur at time step... " +
        this.state.scheduler.getNextTime()
    );
    //
    if (asserts && this.action.person.currentAction != this.action)
      throw new Error(
        "Mismatch between what is happening and what the person think is happening " +
        "( " + this.action.person.currentAction?.constructor.name + " vs " + 
        this.action.constructor.name + ")"

      );

    //time has come to act....
    const actionResolution = this.action.act(this.state);
    console.log(
      "Action completed, the resolution is " + actionResolution.nextAction?.constructor.name
    );

    //this is now the action you are taking
    this.action.person.currentAction = actionResolution.nextAction;

    if (
      actionResolution.waitType == "immediately" &&
      actionResolution.nextAction != null
    )
      new StateMachineStep(actionResolution.nextAction, this.state).update();
    else {
      //if we stop, we stop
      if (
        actionResolution.waitType == "queue" ||
        actionResolution.nextAction == null
      ) {
      }
      //otherwise it HAS to be a number (delay!)
      else {
        this.state.scheduler.scheduleOnceIn(
          new StateMachineStep(actionResolution.nextAction, this.state),
          actionResolution.waitType
        );
      }
    }
    const nextAction = actionResolution.nextAction
    //post-process:
    //if you just entered a queue, check if you can just go in (first one in!)
    if ( nextAction instanceof QueuingAtStation) {
      const foodStation = nextAction.foodStation;
      if(!foodStation.inUse){
        if (foodStation.queue.length == 1 && foodStation.queue[0]== this.action.person ){
        foodStation.inUse = true;
        this.state.scheduler.scheduleOnceIn(
          new StateMachineStep(nextAction, this.state),
          foodStation.servingTime
        );
        }

      }      
    }

    //if you freed a queue, check if there is space for anyone else!
    if(this.action instanceof QueuingAtStation)
    {
      const foodStation = this.action.foodStation;
      
      if(foodStation.inUse)
        throw new Error("food station is in use; it shouldn't be: we just got out")

      if(foodStation.queue.length>0){
        if(asserts && foodStation.queue[0] == this.action.person)
          throw new Error("this person didn't get out of the queue correctly")
        const nextGuy = foodStation.queue[0]
        if(!(nextGuy.currentAction instanceof QueuingAtStation))
          throw new Error("person is waiting, but its action is not waiting...")
        foodStation.inUse=true
        this.state.scheduler.scheduleOnceIn(
          new StateMachineStep(nextGuy.currentAction,this.state),
          foodStation.servingTime
        )

      }
    }
    //if you are waiting for consensus, check for consensus
    if (nextAction instanceof WaitingForConsensus) {
      const group: Group = nextAction.person.group;
      //everybody is done eating, time to make another decision
      if (group.isEverybodyWaitingForADecision()) {
        //shall we eat more?
        const eatMore: boolean = group.shallWeEatMore();
        //all of them go (home or to eat more)
        for (const p of nextAction.person.group.members) {
          if(!(p.currentAction instanceof WaitingForConsensus))
            throw new Error("Can't be right! You need to be waiting for consensus here!")
          p.currentAction.decidedToGo = eatMore;
          new StateMachineStep(p.currentAction, this.state).update();
        }
      }
    }

    //if we are the last one out, empty the table
    if (
      nextAction instanceof Exiting &&
      nextAction.person.group.isEverybodyGone()
    ) {
      if (
        nextAction.person.group == null ||
        nextAction.person.group.assignedTable == null
      )
        throw new Error("No group/table to quit!");

        const freeTable = nextAction.person.group.assignedTable

      freeTable.group = null;
      freeTable.sitting = [];
      //if another group is waiting, now it's the time to sit them!
      const nextGroup: Group | undefined = this.state.groupsWaitingToBeServed.shift()
      if(!(nextGroup === undefined))
      {
        
        sitNewGroupAtNewTable(nextGroup, freeTable, this.state);
      }
    }
  }
}




export const firstStep = function(state: SimulationState){
  return new (class extends SimEvent {
  update() {
    // implement here
    generateNewGroup(state)
  }
  })()
};


